  padding: 0px;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.product_wrapper,
.sales_wrapper,
.others_wrapper,
.settings_wrapper,
.reports_wrapper,
.notfound_wrapper,
.expenses_wrapper,
.dashboard_wrapper {
  width: 100%;
  background-color: #f5f6fa;
  /* border: solid; */
  padding: 2rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@media (max-width: 1025px) {
