.l-navbar {
  /* position: absolute; */
  top: 0;
  left: -30%;
  width: 200px;
  height: 100vh;
  background-color: #ffffff;
  padding: 0.5rem 1rem 0 0;
  transition: 0.5s;
  z-index: 100;
  float: left;
}
.s-navbar {
  /* position: fixed; */
  top: 0;
  left: -30%;
  width: 75px;
  height: 100vh;
  background-color: #ffffff;
  padding: 0.5rem 1rem 0 0;
  transition: 0.5s;
  z-index: 100;
}

.nav {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.nav_link {
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  padding: 0.5rem 0 0.5rem 1.5rem;
  margin: 0 0.5rem;
}

.nav_link_closed {
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  padding: 0.5rem 0 0.5rem 1rem;
  margin: 0 0.5rem;
  color: black !important;
}
.nav_link_closed:hover {
  color: white;
  text-decoration: none !important;
  border-radius: 10px;
  background-color: #223372;
  width: 45px;
  height: 45px;
}

.arrow {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: white;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  position: relative;
  top: 30px;
  right: -20px;
}
.arrow:hover {
  cursor: pointer;
  background-color: rgb(245, 245, 245);
}

.nav_logo-name {
  color: #000;
  font-weight: 900;
}

.nav_link {
  position: relative;
  color: #000;
  margin-bottom: 0.5rem;
  transition: 0.3s;
}

.nav_link:hover {
  color: white;
  text-decoration: none !important;
  border-radius: 10px;
  background-color: #223372;
}

.show {
  left: 0;
}

.body-pd {
  padding-left: calc(68px + 1rem);
}

.active {
  color: white;
  text-decoration: none !important;
}

.active::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 32px;
  background-color: #223372;
  border-radius: 10px;
  z-index: -1;
}

.active_closed::before {
  content: "";
  position: absolute;
  left: 0;
  width: 32px;
  height: 32px;
  background-color: #223372;
  border-radius: 10px;
  z-index: -1;
  margin-left: 1.1rem;
}

@media screen and (min-width: 768px) {
  .l-navbar {
    left: 0;
    padding: 1rem 1rem 0 0;
  }
  .s-navbar {
    left: 0;
    padding: 1rem 1rem 0 0;
  }

  .show {
    width: calc(68px + 156px);
  }
}

